@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);

* {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-seriff;

}

.home, .about, .bib, .data, .future, .results, .sa {
    display: -webkit-flex;
    display: flex;
    height: 100vh;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 3rem;
    font-size: 25px;
    color: #8B0017;
}

.offWhite {
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    background-color: #fffaf0;
}


.nav-text {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    color: black;
}

.nav-text a {
    text-decoration: none;
    color: #000000;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #8B0017;
    color: white;

}

.nav-menu-items {
    width: 100%;

}
nav {
    position: fixed;
    left: 0;
}

span {
    margin-left: 16px;
}


